.offers{
    
    width: 60%;
    height: 75vh;
    margin: auto;    
        display: flex;
        padding: 0px 140px;
        margin-bottom: 150px;
        margin-top: 100px;
        background: linear-gradient(180deg, #fde1ff 0%,#e1ffea22 60%);
    }
    .offers-left{
        flex:1;
        display: flex;
        flex-direction: column;
        justify-content: center;
        gap: 10px
    }
    .offers-left h1{
        color: #171717;
        font-size: 50px;
        font-weight: 700;
    }
    .offers-left p{
        color:#171717;
        font-size:15px;
        font-weight: 600;
    }
    .offers-left button{
        width:282px;
        height: 70px;
        border-radius: 35px;
        background: #ff4141;
        border: none;
        color: white;
        font-size: 22px;
        font-weight: 500;
        margin-top: 30px;
        cursor: pointer;
    
    }
    .offers-right{
        flex:1;
        display: flex;
        align-items: center;
        justify-content: flex-end;
    }
    .offers-right img{
        width:306px;
    }