.cart{
    margin: 80px 150px;
}
.cart hr{
        height: 3px;
        background: #e2e2e2;
        border: 0;
}
.cartitems-fomat-main{
    display: grid;
    grid-template-columns: 0.5fr 2fr 1fr 1fr 1fr 1fr;
    align-items: center;
    gap: 75px;
    padding: 20px 0px;
    color: #454545;
    font-size: 18px;
    font-weight: 600;
}
.cartitems-format{
    font-size: 17px;
    font-weight: 500;
}
.carticon-product-icon{
    height: 62px;
}

.cartItems-remove-icon{
    width: 15px;
    margin: 0px 40px;
    cursor: pointer;
}
.cartitems-quantity{
    width: 64px;
    height: 50px;
    border: 2px solid #ebebeb;
    background: #ffffff;
}

.cartitems-down{
    display: flex;
    margin: 100px 0px ;
}
.cartitems-total{
    flex: 1;
    display: flex;
    flex-direction: column;
    margin-right: 100px;
    gap: 40px;
}
.cartitems-total-item{
    display: flex;
    justify-content: space-around;
    padding: 15px 0px;
}
.cartitems-total button{
    width:262px;
    height: 45px;
    outline: none;
    border: none;
    background: #ff5a5a;
    color: #fff;
    font-size: 16px;
    font-weight: 600;
    cursor: pointer;
}

.cartitems-promocode{
    flex: 1;
    font-size: 16px;
    font-weight: 500;
}
.cartitems-promocode p{
    color: #555;
}

.cartitems-promobox{
    width: 404px;
    
    margin-top: 15px;
    padding-left: 20px;

    background: #eaeaea;
}

.cartitems-promobox input{
    border: none;
    outline: none;
    background: transparent;
    font-size: 16px;
    width: 330px;
    height: 45px;
}
.cartitems-promobox button{
    width: 70px;
    height: 45px;
    font-size: 16px;
    background: black;
    color: white;
    cursor: pointer;
}

