.loginsignup{
    width: 100%;
    height: 95vh;
    background: #fce3fe;
    padding-top: 100px;
}
.loginsignup-container{
    width: 580px;
    height: 450px;
    background: white;
    margin: auto;
    
    padding: 40px 60px;
}
.loginsignup-container p{
    margin: 20px 0px;
}
.loginsignup-fields{
    display: flex;
    flex-direction: column;
    gap: 29px;
    margin-top: 30px;
}
.loginsignup-fields input{
    width:100%;
    height:42px;
    padding-left: 20px;
    border: 1px solid #c9c9c9;
    outline: none;
    font-size: 18px;
    color: #5c5c5c;
}
.loginsignup-container button{
 width: 580px;
 height: 50px;
 color: white;   
 background: #ff4141;
 margin-top: 30px;
 border: none;
 font-size: 24px;
 font-weight: 500;
 cursor: pointer;
}
.loginsignup-login{
    margin-top: 20px;
    color: #5c5c5c;
    font-size: 18px;
    font-weight: 500;
}
.loginsignup-login span{
    color: #ff4141;
    font-weight: 600;
}
.loginsingup-agree{
    display: flex;
    align-items: center;
    margin: 10px;
    gap: 20px;
    color: #5c5c5c;
    font-size: 18px;
    font-weight: 500;
}